import React from 'react'
import user from "../images/user.png";

const ContactCard = (props) => {
    const {id, name ,email,number} = props.contact // destructuring the props to use directly instaded of like props,contact.name etc...
  return (
   <>
    <div className="item" style={{marginTop: '0px',padding:'5px'}}>
        <img className='ui avatar image ' src={user} alt="userIMG" />
            <div className="content">
                <div  className="header">{name}</div>
                <div>{email}</div>
                <div>{number}</div>
            </div>

            <i className='trash alternate outline icon ' style={{color:"red" , marginLeft: "50px"}} onClick={()=>props.clickHandler(id)}></i>
           
    </div>
   </>
  )
}

export default ContactCard
