import React, { Component } from 'react'
//import {Link} from "react-router-dom";

export default class AddContact extends Component {
    state = {
        name:"",
        email:"",
        number:""
    }

    add =(e) =>{
           e.preventDefault() // not to refrash page on submitting
            if(this.state.name ==="" || this.state.number ===""){
                alert("Please enter data");
                return
            }
            else{
                //console.log(this.state) here we got the data enterd in input to console now send it to the parent componant and then to show list 
                // for this pass function as a props for child to perent 
                this.props.addContactHandler(this.state);
                this.setState({name:"",email:"",number:""})
            }
    }
  render() {
    return (
        <>
        <div className='ui main'style={{marginTop: '50px'}}>
        <h2>Enter contact details</h2>
        <form className='ui form'  onSubmit={this.add}>
            <div className='field'>
                <label>Name</label>
                <input type='text' name='name' placeholder='Enter name'value={this.state.name} onChange={(e)=>this.setState({name:e.target.value})}></input>
    
            </div>
            <div className='field'>
                <label>Email</label>
                <input type='email' name='Email' placeholder='Enter mail' value={this.state.email} onChange={(e)=>this.setState({email:e.target.value})} ></input>
    
            </div>
            <div className='field'>
                <label>Number</label>
                <input type='text' name='number' placeholder='Enter number' value={this.state.number} onChange={(e)=>this.setState({number:e.target.value})}></input>
    
            </div>
           
            <button className='ui button blue'>Add</button>
           
        </form>
        </div>
        </>
    )
  }
}

