import React ,{useState,useEffect} from 'react'
import {uuid} from "uuidv4";
import AddContact from './AddContact';
import './App.css';
import ContactList from './ContactList';
import Header from './Header';
//import {BrowserRouter as Router,Routes,Route} from "react-router-dom";
//useEffect to store data on local storage that is browser

// function to fatch data from local storage and pass to setcontact state 
const LOCAL_STORAGE_KEY = "contacts";
const getLocalData =()=>{
  let retrieveContacts = localStorage.getItem(LOCAL_STORAGE_KEY);
  console.log(retrieveContacts);
  if(retrieveContacts){
    return JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY));
  }
  else{
    return [];
  }
}
function App() {
 
 const [contacts,setcontacts] = useState(getLocalData()); // this will be use to add contact and now -- initial value is calling an function getlocal data 

// now we will use hook state to dynamically send contact data or use it 
  // const contacts = [
  //   {
  //     id:"1",
  //     name : "satyam",
  //     email:"satya@gmail.com",
  //     number : "1234567892"
  //   },
  //   {
  //     id:"2",
  //     name : "Ajay",
  //     email:"Ajya@gmail.com",
  //     number : "1234567892"
  //   },
  
  // ]
   const addContactHandler = (contact)=>{
       //console.log(contact) 
       // sending data with uniq id to contactList
       setcontacts([...contacts,{id:uuid(),...contact}]);
       // ...contacts = all previous contacts  + new one added by form
   };

 
   const removeContactHandler = (id)=>{
    const newContactList = contacts.filter((contact)=>{
      return contact.id !==id;
    });
    setcontacts(newContactList);
   };
 

   //useEffect to store data locally need a uniq key 
   useEffect(()=>{
    localStorage.setItem(LOCAL_STORAGE_KEY,JSON.stringify(contacts));
   },[contacts]);



    // retrive the data from local storage  => see list after page is refresh
// not working :(
//  useEffect(() => {
//       const retrieveContacts = JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY));
//       console.log(retrieveContacts);
//       if(retrieveContacts)setcontacts(retrieveContacts);
// }, []);

    
  
  return (
      <>
      <div className="ui container">
      {/* <Router>
        <Header/>
        <Routes>
          
        <Route path='/' exact Component={()=><ContactList contacts = {contacts}  getContactId={removeContactHandler} />}/>


        <Route path='/add' exact Component={()=><AddContact addContactHandler = {addContactHandler}/>}/>

        </Routes>
        </Router> */}
      
      <Header/>
      <AddContact addContactHandler = {addContactHandler}/>
      <ContactList contacts = {contacts}  getContactId={removeContactHandler}/>  
      
      {/* passing props to the conponant */}
      
      </div>
      </>
  );
}

export default App;
